html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

#home-frame {
  border: 3px dashed #ec005a;
  border-radius: 5px;
  padding: 20px;
}

#home-frame > :first-child {
  margin-top: 0;
}

#home-frame > :last-child {
  margin-bottom: 0;
}

#home-frame.snazzy {
  background-color: #fff600;
}

